const { ccclass, property } = cc._decorator;

cc.log('Stick.ts');
@ccclass
export default class Stick extends cc.Component {
    @property(cc.Node)
    stickSprite: cc.Node = null;

    private initialHeight: number = 10;

    onLoad() {
        cc.log('Stick onLoad');
        this.stickSprite.setContentSize(this.stickSprite.width, this.initialHeight);

        this.stickSprite.on(cc.Node.EventType.TOUCH_START, this.onTap, this);
    }

    onTap(event: cc.Event.EventTouch) {
        let newHeight = this.stickSprite.height + 5;
        this.stickSprite.setContentSize(this.stickSprite.width, newHeight);
    }
}
